@import 'helpers';

.scene--front {
  .filter-list {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .filter-list-item {
    list-style: none;
    margin: 0;
    padding: 0;
  }

  .project-list-wrapper {
    margin: 0 auto;
    max-width: 90em;
  }

  .project-list {
    display: flex;
    position: relative;
    flex-flow: row wrap;
    list-style: none;
    margin: -.25em;
    padding: 0;
  }

  .project-list-item {
    display: block;
    position: relative;
    flex: 0 0 50%;
    list-style: none;
    margin: 0;
    padding: 0;
    z-index: 0;
  }

  .project {
    display: block;
    position: relative;
    margin: .25em;

    &__thumb {
      display: block;
      position: relative;

      a {
        display: block;
        border: 1px dashed rgba(black, .3);
      }

      img {
        display: block;
        width: 100%;
        height: auto;
        opacity: 0;
      }
    }

    &__label {
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      background: white;
      padding: .25em .5em;
      line-height: $line-height-heading;
    }
  }

  @include breakpoint($screen-small) {
    .project-list {
      margin: -.5em;
    }

    .project {
      margin: .5em;

      &__label {
        padding: .5em .75em;
      }
    }
  }

  @include breakpoint($screen-medium) {
    .project-list {
      display: block;
      margin: 0 auto;
      padding: 4.75% 0;
    }

    .project-list-item {
      flex: none;
      top: 0;
      width: 32%;

      &:nth-child(5n+1) {
        left: 0;
        margin-top: -5%;
      }

      &:nth-child(5n+2) {
        left: 34%;
        margin-top: -22.55%;
      }

      &:nth-child(5n+3) {
        left: 68%;
        margin-top: -22.55%;
      }

      &:nth-child(5n+4) {
        left: 17%;
        margin-top: -5%;
      }

      &:nth-child(5n+5) {
        left: 51%;
        margin-top: -22.55%;
      }

      &:nth-child(1) {
        left: 0;
        margin-top: 0;
      }
    }

    .project {
      margin: 0;

      &__thumb {
        margin: auto;
        transform: rotate(45deg);
        width: 70.671378091872791519434628975265%; // TODO Replace magic number
      }

      &__label {
        top: 50%;
        bottom: auto;
        left: 50%;
        cursor: pointer;
      }
    }
  }

  @include breakpoint($screen-large) {
    .project-list-item {
      &:nth-child(1) {
        left: 34%;
        margin-top: 0;
      }

      &:nth-child(2) {
        left: 68%;
        margin-top: -22.55%;
      }

      &:nth-child(3) {
        left: 17%;
        margin-top: -5%;
      }

      &:nth-child(4) {
        left: 51%;
        margin-top: -22.55%;
      }

      &:nth-child(5) {
        left: 34%;
        margin-top: -5%;
      }

      &:nth-child(6) {
