      &:nth-child(8) {
        left: 51%;
        margin-top: -22.55%;
      }

      &:nth-child(9) {
        left: 0;
        margin-top: -5%;
      }

      &:nth-child(10) {
        left: 34%;
        margin-top: -22.55%;
