        margin-top: -22.55%;
      }

      &:nth-child(14) {
        left: 0;
        margin-top: -5%;
      }

      &:nth-child(15) {
        left: 34%;
        margin-top: -22.55%;
      }

      &:nth-child(16) {
        left: 17%;
        margin-top: -5%;
      }

      &:nth-child(17) {
        left: 51%;
        margin-top: -22.55%;
      }

      &:nth-child(18) {
