        margin-top: -5%;
      }
    }
  }

  @include breakpoint($screen-xlarge) {
    .project-list {
      padding: 3.5% 0;
    }

    .project-list-item {
      width: 23.5%;

      &:nth-child(7n+1) {
        left: 12.75%;
        margin-top: -4%;
      }

      &:nth-child(7n+2) {
        left: 38.25%;
        margin-top: -16.6%;
      }

      &:nth-child(7n+3) {
        left: 63.75%;
        margin-top: -16.6%;
      }

      &:nth-child(7n+4) {
        left: 0;
        margin-top: -4%;
      }

      &:nth-child(7n+5) {
        left: 25.5%;
        margin-top: -16.6%;
      }

      &:nth-child(7n+6) {
        left: 51%;
