      }

      &:nth-child(9) {
        left: 0;
        margin-top: -4%;
      }

      &:nth-child(10) {
        left: 51%;
        margin-top: -16.6%;
      }

      &:nth-child(11) {
        left: 76.5%;
        margin-top: -16.6%;
      }

      &:nth-child(12) {
        left: 12.75%;
        margin-top: -4%;
      }

      &:nth-child(13) {
        left: 38.25%;
        margin-top: -16.6%;
      }

      &:nth-child(14) {
