.no-touchevents {
  .scene--front {
    .project {
      &__label {
        opacity: 0;
      }
    }
  }

  &.no-js {
    .scene--front {
      .project-list-item {
        &:hover {
          z-index: 100;

          .project {
            &__label {
              background: $yellow;
              opacity: 1;
            }
          }
        }
      }
    }
  }

  &.js {
    .scene--front {
      .project-list-item {
        &.hover {
          z-index: 100;

          .project {
            &__label {
              background: $yellow;
              opacity: 1;
            }
          }
        }
      }
    }
  }
}
